@import url(https://fonts.googleapis.com/css2?family=Cormorant+Unicase:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
    padding: 0;
    margin: 0;
}

.filmBox {
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.imageBox {
    margin-top: 30px;
    width: 300px;
    padding: 0 20px;
    height: 500px;
    -webkit-transform: rotateX(45deg) rotateZ(-25deg);
            transform: rotateX(45deg) rotateZ(-25deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: all 0.5s;
    cursor: pointer;
}
.imageBox:hover {
    -webkit-transform: rotateX(0) translateY(0) scale(1.1);
            transform: rotateX(0) translateY(0) scale(1.1);
    overflow: hidden;
    position: relative;
    z-index: 9999;
}
.imageBox:hover h3 {
    color: #212121;
}
.imageBox img {
    width: 100%;
    height: 400px;
    vertical-align: bottom;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: .3s;
}
.imageBox img:hover {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    
}
.imageBox h3 {
    padding-top: 20px;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    color: #bdbdbd;
}
section h1 {
    font-size: 30px;
    text-align: center;
    padding: 50px 0 0 0;
    letter-spacing: 1rem;
    font-family: 'Cormorant Unicase', serif;
    color: #212121;
}
a {
    text-decoration: none;
    color: #616161;
    font-size: 16px;
    font-family: 'Cormorant Unicase', serif;
}

button {
    border-radius: 4px;
    width: 150px;
    height: 40px;
    background: none;
    border: none;
    color: #616161;
    cursor: pointer;
    display: inline-block;
    margin: 30px 0 50px 0;
    outline: none;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    text-transform: uppercase;
  }
  button:before,
  button:after {
    border-color: transparent;
    transition: all 0.25s;
    border-style: solid;
    border-width: 0;
    content: "";
    height: 24px;
    position: absolute;
    width: 24px;
  }
  button:before {
    border-color: silver;
    border-right-width: 2px;
    border-top-width: 2px;
    right: -5px;
    top: -5px;
  }
  button:after {
    border-bottom-width: 2px;
    border-color: silver;
    border-left-width: 2px;
    bottom: -5px;
    left: -5px;
  }
  button:hover:before,
  button.hover:before,
  button:hover:after,
  button.hover:after {
    height: 100%;
    width: 100%;
  }
.detailBox {
    width: 40%;
    padding: 20px;
    text-align: center;
    margin: 20px 0;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.box {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.detailBox h2 {
    font-family: 'Cormorant Unicase', serif;
    line-height: 36px;
    padding-bottom: 15px;
}
.detailBox h4 {
    font-size: 16px;
    color: #222222;
}
.detailBox .romanised {
    font-size: 12px;
    padding-bottom: 15px;
    color: #222222;
}
.detailBox .date {
    padding-top: 12px;
    letter-spacing: 8px;
}
.detailBox .score {
    color: silver;
    padding: 12px 0;
}
.detailBox .descrip {
    color: #616161;
    text-align: justify;
    padding-top: 20px;
}
